<template>
  <swiper @change="changeActiveIndex">
    <swiper-item v-for="(item, index) in labelList" :key="index">
      <view>{{ item.name }}</view>
    </swiper-item>
  </swiper>
</template>

<script>
export default {
  name: "ArticleList",
  props: {
    labelList: {
      type: Array,
      default: () => [],
    },
    activeIndex: {
      type: Number,
      default: 0,
    },
  },
  data() {
    return {};
  },
  methods: {
    changeActiveIndex(e) {
      this.$emit("changeActiveIndex", e.detail.current);
    },
  },
};
</script>

<style lang="scss"></style>
